<template lang="pug">
#userreserve
  v-card
    v-card-title
      h5 
    v-card-text
      v-form.px-3
        v-select(v-model="form.teethitems" :items="teethitems" label='' prepend-icon='mdi-hospital-box-outline')
        v-select(v-model="form.doctoritems" :items="doctoritems" label='' prepend-icon='mdi-doctor')
        v-text-field(v-model='form.time' label='()' prepend-icon='mdi-clock-time-four-outline')
        v-text-field(v-model='form.phone' label='' prepend-icon='mdi-message-processing-outline')
    v-card-actions
      v-btn.mx-0.mt-3(flat='' @click="resetForm") 
      v-btn.mx-0.mt-3(flat='' @click="addReserve") 
</template>

<script>

export default {
  name: 'UserReserve',
  data () {
    return {
      form: {
        teethitems: ['', '', '', '', ''],
        doctoritems: ['', '', ''],
        time: '',
        phone: ''
      },
      menu: false,
      teethitems: ['', '', '', '', ''],
      doctoritems: ['', '', '']
    }
  },
  methods: {
    resetForm (event) {
      this.form = {
        teethitems: '',
        doctoritems: '',
        time: '',
        phone: ''
      }
    },
    async addReserve () {
      try {
        await this.axios.post('/users/reserve', { teethitems: this.form.teethitems, doctoritems: this.form.doctoritems, time: this.form.time, phone: this.form.phone }, {
          headers: {
            authorization: 'Bearer ' + this.$store.state.jwt.token
          }
        })
        this.$swal({
          icon: 'success',
          title: '',
          text: ''
        })
        this.$router.push('/user')
      } catch (error) {
        this.$swal({
          icon: 'error',
          title: '',
          text: ''
        })
      }
    }
  }
}
</script>
